import React, { useState, useContext } from 'react';
import globalContext from '../context/global/globalContext';
import todoContext from '../context/todos/todoContext';
import Confetti from './Confetti';

function TodoItem(props) {
  const { todo, updateTodoBtn, updateTodo } = props;
  const [status, setStatus] = useState(todo.status);

  const [confetti, setConfetti] = useState(false);

  const gcontext = useContext(globalContext);
  const { showAlert } = gcontext;

  const context = useContext(todoContext);
  const { deleteTodo, statusTodo } = context;

  return (
    <>
      {confetti && <Confetti></Confetti>}
      <div className="row todoitem glassBox">
        <div className="col-1">
          <input
            className="form-check-input"
            type="checkbox"
            id="checkbox"
            checked={status === 'complete' ? true : false}
            onChange={() => {
              setStatus(status === 'complete' ? 'pending' : 'complete');
              statusTodo(
                todo._id,
                status === 'complete' ? 'pending' : 'complete'
              );
              if (status === 'pending') {
                setConfetti(true);
                setTimeout(() => {
                  setConfetti(false);
                }, 2000);
              }
            }}
            aria-label="..."
            style={{ height: '25px', width: '25px', display: 'inline-block' }}
          />
        </div>
        <div className="col-9 align-middle">
          <p style={{ margin: 'auto', paddingLeft: '10px' }}>
            {status === 'complete' ? <del>{todo.todo}</del> : todo.todo}
          </p>
        </div>
        <div className="col-1">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            height="20"
            width="17.5"
            viewBox="0 0 448 512"
            type="button"
            onClick={() => {
              deleteTodo(todo._id);
              showAlert('Deleted Successfully', 'success');
            }}
            style={{ fill: 'white' }}
          >
            <path d="M135.2 17.7C140.6 6.8 151.7 0 163.8 0H284.2c12.1 0 23.2 6.8 28.6 17.7L320 32h96c17.7 0 32 14.3 32 32s-14.3 32-32 32H32C14.3 96 0 81.7 0 64S14.3 32 32 32h96l7.2-14.3zM32 128H416V448c0 35.3-28.7 64-64 64H96c-35.3 0-64-28.7-64-64V128zm96 64c-8.8 0-16 7.2-16 16V432c0 8.8 7.2 16 16 16s16-7.2 16-16V208c0-8.8-7.2-16-16-16zm96 0c-8.8 0-16 7.2-16 16V432c0 8.8 7.2 16 16 16s16-7.2 16-16V208c0-8.8-7.2-16-16-16zm96 0c-8.8 0-16 7.2-16 16V432c0 8.8 7.2 16 16 16s16-7.2 16-16V208c0-8.8-7.2-16-16-16z" />
          </svg>
        </div>
        <div className="col-1">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            height="20"
            width="20"
            viewBox="0 0 512 512"
            type="button"
            data-bs-toggle="modal"
            data-bs-target={`#${updateTodoBtn}`}
            onClick={() => {
              updateTodo(todo);
            }}
            style={{ fill: 'white' }}
          >
            <path d="M471.6 21.7c-21.9-21.9-57.3-21.9-79.2 0L362.3 51.7l97.9 97.9 30.1-30.1c21.9-21.9 21.9-57.3 0-79.2L471.6 21.7zm-299.2 220c-6.1 6.1-10.8 13.6-13.5 21.9l-29.6 88.8c-2.9 8.6-.6 18.1 5.8 24.6s15.9 8.7 24.6 5.8l88.8-29.6c8.2-2.7 15.7-7.4 21.9-13.5L437.7 172.3 339.7 74.3 172.4 241.7zM96 64C43 64 0 107 0 160V416c0 53 43 96 96 96H352c53 0 96-43 96-96V320c0-17.7-14.3-32-32-32s-32 14.3-32 32v96c0 17.7-14.3 32-32 32H96c-17.7 0-32-14.3-32-32V160c0-17.7 14.3-32 32-32h96c17.7 0 32-14.3 32-32s-14.3-32-32-32H96z" />
          </svg>
        </div>
      </div>
    </>
  );
}

export default TodoItem;
